import React from "react";
import { Link } from "gatsby";
import Img from "gatsby-image";

const PhotoExcerpt = ( {data} ) => {
	const featuredImg = data.frontmatter.image.childImageSharp;
	
	return (
		<article className="PhotoExcerpt mb-M">
			<Link to={`/${data.fields.slug}`}>
				<h2 className="mb-S">
					{data.frontmatter.title}
				</h2>
				<Img fluid={featuredImg.fluid} sizes={{...featuredImg.fluid, aspectRatio: 1 }} />
			</Link>
		</article>
	)
}

export default PhotoExcerpt

