import React from "react";
import { graphql } from "gatsby";

import PhotoExcerpt from "../components/PhotoExcerpt";
import Layout from "../components/layout";

export default ({ data }) => {
	return (
		<Layout>
			<h1>
				Photography
			</h1>
			<section className="flex-wrap flex-size-auto">
				{ data.allMarkdownRemark.edges.map(({ node }) => ( <PhotoExcerpt key={ node.id } data={ node } /> )) }
			</section>	
		</Layout>
	)
}

export const query = graphql`
query{
	allMarkdownRemark(
		filter: { 
			frontmatter:{
				date: {ne: null}
				published: {eq: true}
				category: {eq: "photos"}
			}
		},
		sort: {
			order: DESC,
			fields: frontmatter___date
		}
	) {
		totalCount
		edges {
			node {
				id
				frontmatter { 
					title
					date
					category
					description
					image {
	  				childImageSharp {
							fluid(maxWidth: 800) {
								...GatsbyImageSharpFluid
							}
						}
					}
				}
				fields {
					slug
				}
				excerpt(format: HTML)
			}
		}
	}
}`
